			"innopolis1": {
				"company": {
					"ru": "«Иннополис»",
					"en": "Innopolis"
				},
				"text1": {
					"ru": "<b>Фирменный стиль</b> <br /> «Иннополиса»",
					"en": "<b>Innopolis</b> <br /> brand identity"
				},
				"text2": {
					"ru": "<b>Задача:</b> <br /> Разработать фирменный стиль для нового города.<br /><br /> «Иннополис» — новый российский город, расположенный в Республике Татарстан, призванный стать центром развития высоких технологий и местом жизни молодых специалистов.<br /> В «Иннополисе» есть собственная городская экосистема, пять тысяч местных жителей и амбициозные планы на ближайшее будущее. <br /><br /> В «Кайндбитле» разработали фирменный стиль и герб, призванный помочь всем начинаниям свершиться.\n",
					"en": "<b>Task:</b> <br /> Design a corporate identity for a new city.<br /><br /> Innopolis is a new city in the Republic of Tatarstan in Russia. It is built to become a center for innovation and technology, and home to young industry professionals.<br /> Innopolis has its own urban ecosystem, a population of five thousand people, and ambitious plans for the future.<br /><br /> Here at Kindbeetle, we designed a brand identity and a coat of arms to help Innopolis achieve its goals.\n"
				},
				"text3": {
					"ru": "1 января 2016",
					"en": "January 1 2016"
				},
				"text4": {
					"ru": "Основная идея знака — графические образы микросхемы и щита. Микросхема как единица и суть всех электронных технологий. Щит как древний символ защиты и укрепления.\n",
					"en": "The symbol looks like both a microchip and a shield. The microchip is a metaphor for electronics technology, while the shield is a symbol of protection and strength.\n"
				},
				"text5": {
					"ru": "Крылатый снежный барс — один из символов Татарстана. Поэтому за основу герба «Иннополиса» решено было взять именно его. Ак барс держит в лапах ноутбук, как один из инструментов коммуникации, без которого не обходится ни один специалист.\n",
					"en": "Aq Bars, a winged snow leopard, appears on the coat of arms of Tatarstan. On that of Innopolis, this legendary creature is shown holding a laptop—one of the things today’s professionals use daily in their work.\n"
				},
				"text6": {
					"ru": "В «Кайндбитле» также разработали",
					"en": "We developed more than one"
				},
				"text7": {
					"ru": "ещё один концепт",
					"en": "design concept"
				},
				"text8": {
					"ru": "фирменного стиля для «Иннополиса».",
					"en": "for Innopolis."
				}
			},
