					"en": "<br/> McKinsey & Company holds an annual business case competition for students. Real-life situations presented to participants allow them to try on the role of business consultants. Those who come out on top are invited to join the McKinsey team. <br/><br/> Here at Kindbeetle, we created a website for this purpose. We developed a content management system that enables competitors to upload their submissions, and the jury to review and manage the materials, and get in touch with the winners.\n"
