import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import linkify from 'components/linkify';
import links from 'data/links.yml';

export default class Tutor extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>{linkify(text.pages.header3[__LANG__])}</div>
            <div className={styles.cols}>
              <div className={styles.textDiv}>
                <h2 className={styles.text2}>{linkify(text.pages.tutorme.text1[__LANG__])}</h2>
                <div className={styles.text3}>
                  <p>{linkify(text.pages.tutorme.text2[__LANG__])}</p>
                  <p>
                    <a href={links.tutorme}
                       target='_blank'>{linkify(text.pages.tutorme.company[__LANG__])}</a> {linkify(text.pages.tutorme.text3[__LANG__])}
                  </p>
                  <p className={styles.text4}>{linkify(text.pages.tutorme.text4[__LANG__])}</p>
                </div>
              </div>
              <div className={styles.divX}>
                <div className={styles.img1}></div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.divY}>
          <div className={styles.div2}>
            <div className={styles.img2}></div>
          </div>
          <div className={styles.root}>
            <div className={styles.content}>
              <div className={styles.text3}>
                {linkify(text.pages.tutorme.text5[__LANG__])}
              </div>
            </div>
          </div>
          <div className={styles.div2}>
            <div className={styles.img3}></div>
          </div>
          <div className={styles.root}>
            <div className={styles.content}>
              <div className={styles.text5}>
                {linkify(text.pages.tutorme.text6[__LANG__])}
              </div>
            </div>
          </div>
          <div className={styles.div4}>
            <iframe src='https://www.youtube.com/embed/hryrak6B7_s'></iframe>
          </div>
        </div>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text5}>
              {linkify(text.pages.tutorme.text7[__LANG__])}
            </div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback/>
      </InnerPage>
    );
  }

}



