import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import linkify from 'components/linkify';

export default class KrasniyKarandash extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
        <div className={styles.header}>
          <div className={styles.content}>
            <div className={styles.headerTitle}>{linkify(text.pages.header[__LANG__])}</div>
            <div className={styles.cols}>
              <div className={styles.wrapInfo}>
                <h2 className={styles.title}>{linkify(text.pages.kk.text1[__LANG__])}</h2>
                <div className={styles.info}>
                  <p>{linkify(text.pages.kk.text2[__LANG__])}</p>
                  <p>{linkify(text.pages.kk.text3[__LANG__])}</p>
                  <p className={styles.infoFooter}>{linkify(text.pages.kk.text4[__LANG__])}</p>
                </div>
              </div>
              <div className={styles.logo1} />
            </div>
          </div>
        </div>
          <div className={styles.mainContent}>
            <div className={styles.logo2Wrap}>
              <div className={styles.logo2}/>
            </div>
            <div className={styles.info2}>{linkify(text.pages.kk.text5[__LANG__])}</div>
            <div className={styles.img2}/>
            <div className={styles.img3}/>
            <div className={styles.info2}>{linkify(text.pages.kk.text6[__LANG__])}</div>
            <div className={styles.img4}/>
            <h3 className={styles.note}>
              {linkify(text.pages.kk.text7[__LANG__])}
            </h3>
          </div>
          <div className={styles.border}></div>
          <Feedback/>
        </div>
      </InnerPage>
    );
  }

}



