import React from 'react';
import { Route } from 'react-router';

import Root from 'routes/Root';
import Home from 'routes/Home';
import XsollaPage1 from 'routes/XsollaPage1';
import XsollaPage2 from 'routes/XsollaPage2';
import Innopolis1 from 'routes/Innopolis1';
import Innopolis2 from 'routes/Innopolis2';
import Betmaster from 'routes/Betmaster';
import Tutor from 'routes/Tutor';
import KrasniyKarandash from 'routes/KrasniyKarandash';
import McKinsey from 'routes/McKinsey';
import Vdnh from 'routes/Vdnh';

export default (
  <Route component={Root}>
    <Route path={`${__PREFIX__}/`} component={Home}/>
    <Route path={`${__PREFIX__}/xsolla1`} component={XsollaPage1}/>
    <Route path={`${__PREFIX__}/xsolla2`} component={XsollaPage2}/>
    <Route path={`${__PREFIX__}/innopolis1`} component={Innopolis1}/>
    <Route path={`${__PREFIX__}/innopolis2`} component={Innopolis2}/>
    <Route path={`${__PREFIX__}/betmaster`} component={Betmaster}/>
    <Route path={`${__PREFIX__}/tutorme`} component={Tutor}/>
    <Route path={`${__PREFIX__}/krasniy-karandash`} component={KrasniyKarandash}/>
    <Route path={`${__PREFIX__}/mckinsey`} component={McKinsey}/>
    <Route path={`${__PREFIX__}/vdnh`} component={Vdnh}/>
  </Route>
);



