/*eslint-disable */

import React from 'react';
import validator from 'validator';

import links from 'data/links.yml';

const linkComponents = {};

Object.keys(links).forEach(name => {
  const rawLink = links[name];
  let component = 'span';
  let props = null;
  if (validator.isEmail(rawLink)) {
    component = 'a';
    props = {href: `mailto:${rawLink}`};
  } else if (validator.isURL(rawLink)) {
    component = 'a';
    props = {href: rawLink};
  }
  linkComponents[name] = key => React.createElement(component, {...props, key}, rawLink);
});

const varRegex = /#{(\w+)}/;

export default function linkify(source) {
  const result = [];
  let rest = source;
  while (rest) {
    const match = varRegex.exec(rest);
    if (!match) {
      result.push(rest);
      break;
    }
    if (match.index) {
      result.push(rest.substring(0, match.index));
    }
    result.push(rest.substring(match.index, match.index + match[0].length));
    rest = rest.substring(match.index + match[0].length);
  }
  return result.map((item, key) => {
    const match = varRegex.exec(item);
    if (match && linkComponents[match[1]]) {
      return linkComponents[match[1]](key);
    }
    return <span key={key} dangerouslySetInnerHTML={{__html: item}} />;
  });
}



