import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import links from 'data/links.yml';
import linkify from 'components/linkify';

export default class Innopolis2 extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>{linkify(text.pages.header[__LANG__])}</div>
            <h2 className={styles.text2}>{linkify(text.pages.innopolis2.text1[__LANG__])}</h2>
            <div className={styles.cols}>
              <div className={styles.text3}>
                <p>{linkify(text.pages.innopolis2.text2[__LANG__])}</p><br />
                <p>{linkify(text.pages.innopolis2.text3[__LANG__])} <a
                  href={links.innopolis1[__LANG__]}>{linkify(text.pages.innopolis2.text4[__LANG__])}</a>.
                </p><br />
                <p>{linkify(text.pages.innopolis2.text5[__LANG__])}</p>
                <p className={styles.text4}>{linkify(text.pages.innopolis2.text6[__LANG__])}</p>
              </div>
              <div className={styles.divX}>
                <div className={styles.img1}></div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.img2}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>{linkify(text.pages.innopolis2.text7[__LANG__])}</p>
            </div>
            <div className={styles.img3}></div>
            <div className={styles.img4}></div>
          </div>
        </div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>{linkify(text.pages.innopolis2.text8[__LANG__])}</p>
            </div>
          </div>
        </div>
        <div className={styles.img6}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>{linkify(text.pages.innopolis2.text9[__LANG__])} <a
                href={links.innopolis1[__LANG__]}>{linkify(text.pages.innopolis2.text10[__LANG__])}</a> {linkify(text.pages.innopolis2.text11[__LANG__])}
              </p>
            </div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback />
      </InnerPage>
    );
  }

}



