import React from 'react';
import Component from 'react-pure-render/component';
import classnames from 'classnames';
import ReactSwipe from 'react-swipe/react-swipe';

import linkify from 'components/linkify';
import text from 'data/text.yml';
import ArrowRight from 'icons/arrow-right.svg';

text.quotes.forEach(quote => {
  if (!/^(https?:)/.test(quote.pic)) {
    try {
      quote.pic = require(`data/faces/${quote.pic}`);
    } catch (err) {
      // lol ok
    }
  }
});

export default class Swiper extends Component {

  constructor() {
    super();
    this.state = {
      step: 0
    };
  }

  goTo = step => {
    this.setState({step});
    this.refs.sw.swipe.slide(step);
  };

  goToPrev = () => {
    const sw = this.refs.sw.swipe;
    const step = sw.getPos() === 0
      ? sw.getNumSlides() - 1
      : this.state.step - 1;
    this.setState({step});
    this.refs.sw.swipe.prev();
  };

  goToNext = () => {
    const sw = this.refs.sw.swipe;
    const step = sw.getPos() === sw.getNumSlides() - 1
      ? 0
      : this.state.step + 1;
    this.setState({step});
    this.refs.sw.swipe.next();
  };

  onSwipe = step => {
    this.setState({step});
  };

  render() {
    const panes = text.quotes.map((item, i) =>
        <div key={i} className={styles.pane}>
          <div className={styles.panePic} style={{backgroundImage: `url(${item.pic})`}}/>
          <div className={styles.paneName}>{item.name[__LANG__]}</div>
          <div className={styles.panePosition}>{item.position[__LANG__]}</div>
          <div className={styles.paneMotto}>{linkify(item.motto[__LANG__])}</div>
        </div>
    );

    const dots = text.quotes.map((item, i) =>
        <div key={i} className={classnames(styles.dot, i === this.state.step && styles.dotActive)}
             onClick={this.goTo.bind(this, i)}>
        </div>
    );

    return (
      <div className={styles.root}>
        <div className={styles.content}>
          <ReactSwipe ref='sw' className={styles.swiper} slideToIndex={0} callback={this.onSwipe}>
            {panes}
          </ReactSwipe>

          <div className={styles.dotsContainer}>
            <div className={styles.dots}>
              {dots}
            </div>
          </div>

          <div className={styles.arrowPrev} onClick={this.goToPrev}>
            <ArrowRight />
          </div>

          <div className={styles.arrowNext} onClick={this.goToNext}>
            <ArrowRight />
          </div>
        </div>
      </div>
    );
  }

}



