import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import linkify from 'components/linkify';

export default class Vdnh extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.header}>
            <div className={styles.content}>
              <div className={styles.headerTitle}>{linkify(text.pages.header[__LANG__])}</div>
              <h2 className={styles.title}>{linkify(text.pages.vdnh.text1[__LANG__])}</h2>
              <div className={styles.info}>
                <p>{linkify(text.pages.vdnh.text2[__LANG__])}</p>
                <p>{linkify(text.pages.vdnh.text3[__LANG__])}</p>
                <p className={styles.infoFooter}>{linkify(text.pages.vdnh.text4[__LANG__])}</p>
              </div>
            </div>
            <div className={styles.logo1Wrap}>
              <div className={styles.logo1}/>
            </div>
          </div>
          <div className={styles.logo2Wrap}>
            <div className={styles.logo2}/>
          </div>
          <div className={styles.info2}>
            {linkify(text.pages.vdnh.text5[__LANG__])}
            {linkify(text.pages.vdnh.text6[__LANG__])}
            </div>
          <div className={styles.imgRight}>
            <div className={styles.img2}/>
          </div>
          <div className={styles.imgLeft}>
            <div className={styles.img3}/>
          </div>
          <div className={styles.info2}>{linkify(text.pages.vdnh.text7[__LANG__])}</div>
          <div className={styles.img4}/>
          <div className={styles.info2}>{linkify(text.pages.vdnh.text8[__LANG__])}</div>

          <div className={styles.cards}>
            <div className={styles.card}>
              <div className={styles.img5}/>
            </div>
            <div className={styles.card}>
              <div className={styles.img6}/>
            </div>
            <div className={styles.card}>
              <div className={styles.img7}/>
            </div>
          </div>
          <div className={styles.cards}>
            <div className={styles.card}>
              <div className={styles.img8}/>
            </div>
            <div className={styles.card}>
              <div className={styles.img9}/>
            </div>
            <div className={styles.card}>
              <div className={styles.img10}/>
            </div>
          </div>
          <div className={styles.cards}>
            <div className={styles.card}>
              <div className={styles.img11}/>
            </div>
            <div className={styles.card}>
              <div className={styles.img12}/>
            </div>
            <div className={styles.card}>
              <div className={styles.img13}/>
            </div>
          </div>

          <div className={styles.border} />
          <Feedback/>
        </div>
      </InnerPage>
    );
  }

}



