import React from 'react';
import Component from 'react-pure-render/component';

export default class CompletedProjects extends Component {

  render() {
    return (
      <div className={styles.root}>
        <div className={styles.content}>
          <div className={styles.logos}>
            <div className={styles.logoAmway}/>
            <div className={styles.logoAeroflot}/>
            <div className={styles.logoInnova}/>
            <div className={styles.logoMvideo}/>
            <div className={styles.logoRzd}/>
            <div className={styles.logoTechnosila}/>
            <div className={styles.logoMoscow}/>
            <div className={styles.logoTinkoff}/>
          </div>
        </div>
      </div>
    );
  }

}



