import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import text from 'data/text.yml';
import links from 'data/links.yml';
import linkify from 'components/linkify';

export default class Betmaster extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>{linkify(text.pages.header2[__LANG__])}</div>
            <div className={styles.cols}>
              <div className={styles.col1}>
                <h2 className={styles.text2}>{linkify(text.pages.betmaster.text1[__LANG__])}</h2>
                <a href={links.betmaster} target='_blank'>
                  <div className={styles.logo}></div>
                </a>
                <div className={styles.stores}>
                  <a href={links.betmasterApp} target='_blank'>
                    <div className={styles.appStore}></div>
                  </a>
                  <a href={links.betmasterG} target='_blank'>
                    <div className={styles.gStore}></div>
                  </a>
                </div>
                <div className={styles.text3}>
                  <p>{linkify(text.pages.betmaster.text2[__LANG__])}</p>
                  <p>
                    <a href={links.betmaster}
                       target='_blank'>{linkify(text.pages.betmaster.company[__LANG__])}</a> {linkify(text.pages.betmaster.text3[__LANG__])}
                  </p>
                  <p className={styles.text4}>{linkify(text.pages.betmaster.text4[__LANG__])}</p>
                </div>
              </div>
              <div className={styles.col2}>
                <div className={styles.img1}></div>
              </div>
            </div>
            <div className={styles.text5}>
              <p>{linkify(text.pages.betmaster.text5[__LANG__])} <a
                href={links.betmaster} target='_blank'>betmaster.com</a> {linkify(text.pages.betmaster.text6[__LANG__])}
              </p>
            </div>
          </div>
        </div>
        <div className={styles.img2}></div>
        <div className={styles.img3}></div>
        <div className={styles.border}></div>
        <Feedback/>
      </InnerPage>
    );
  }

}



