import React from 'react';
import Component from 'react-pure-render/component';

export default class Spinner extends Component {

  render() {
    return (
      <div className={styles.root}>
        <div className={styles.bounce1}/>
        <div className={styles.bounce2}/>
        <div />
      </div>
    );
  }

}



